@import '../../assets/scss/helpers';

.item {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 16px;

  .thumbnail {
    position: relative;
    overflow: hidden;
    width: 120px;
    height: 120px;
    border-radius: 4px;
    background-color: $lightgray;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    .proof {
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      min-width: 28px;
      height: 28px;
      align-items: center;
      justify-content: center;
      padding: 0 4px;
      border-radius: 0 4px 0 0;
      background-color: $error;
      color: $white;
      font-size: 10px;
      font-weight: 800;
    }
  }

  .content {
    .title {
      font-size: 16px;
      font-weight: 700;

      small {
        display: block;
        color: $darkgray;
        font-size: 12px;
      }
    }

    .category {
      margin-top: 4px;
      font-size: 12px;
    }
  }
}
