@import '../../assets/scss/helpers';

.now-loading {
  display: flex;
  min-height: 200px;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  gap: 12px;
  text-align: center;

  .icon {
    animation: loading-spin 2s infinite cubic-bezier(.81,.13,.24,.75);
    color: $darkgray;
    font-size: 28px;
  }

  .text {
    color: $black;
    font-size: 12px;
  }

  @keyframes loading-spin {
    0% {
      transform: rotate(0deg);
    }
    80%, 100% {
      transform: rotate(180deg);
    }
  }
}
